/// @requires {Map} $indigo-elevation-input-group
$indigo-input-group: extend(
    $light-input-group,
    $indigo-elevation-input-group,
    (
        input-prefix-color: (
            color: (
                'gray',
                600,
            ),
        ),
        input-suffix-color: (
            color: (
                'gray',
                600,
            ),
        ),
        input-prefix-color--filled: (
            color: (
                'gray',
                600,
            ),
        ),
        input-suffix-color--filled: (
            color: (
                'gray',
                600,
            ),
        ),
        input-prefix-color--focused: (
            color: (
                'gray',
                600,
            ),
        ),
        input-suffix-color--focused: (
            color: (
                'gray',
                600,
            ),
        ),
        helper-text-color: (
            color: (
                'gray',
                600,
            ),
        ),
        border-color: (
            color: (
                'gray',
                400,
            ),
        ),
        hover-border-color: (
            color: (
                'gray',
                600,
            ),
        ),
        focused-border-color: (
            color: (
                'primary',
                400,
            ),
        ),
        disabled-border-color: (
            color: (
                'gray',
                300,
            ),
        ),
        idle-bottom-line-color: (
            color: (
                'gray',
                500,
            ),
        ),
        focused-bottom-line-color: (
            color: (
                'secondary',
                500,
            ),
        ),
        hover-bottom-line-color: (
            color: (
                'gray',
                500,
            ),
        ),
        disabled-bottom-line-color: (
            color: (
                'gray',
                300,
            ),
        ),
        box-background: transparent,
        box-disabled-background: transparent,
        box-background-hover: (
            color: (
                'gray',
                100,
            ),
        ),
        idle-text-color: (
            color: (
                'gray',
                600,
            ),
        ),
        disabled-text-color: (
            color: (
                'gray',
                300,
            ),
        ),
        placeholder-color: (
            color: (
                'gray',
                300,
            ),
        ),
        disabled-placeholder-color: (
            color: (
                'gray',
                300,
            ),
        ),
        box-border-radius: (
            border-radius: (
                rem(0),
                rem(0),
                rem(20px),
            ),
        ),
        border-border-radius: (
            border-radius: (
                rem(0),
                rem(0),
                rem(20px),
            ),
        ),
        search-border-radius: (
            border-radius: (
                rem(2px),
                rem(0),
                rem(20px),
            ),
        ),
        idle-secondary-color: (
            color: (
                'gray',
                900,
            ),
        ),
    )
);
